@import url(https://fonts.googleapis.com/css2?family=La+Belle+Aurore&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #2b2d42;
  color: hsl(0, 0%, 100%);
  font-family: 'lato', sans-serif;
}

button {
  cursor: pointer;
  text-align: center;
  background-color: crimson;
  border: none;
  padding: 5px 10px;
  font-weight: bold;
}
.container {
  width: 80%;
  margin: 5% auto;
}
ul {
    display: flex;
    list-style: none;
    flex-wrap: wrap;

}

li {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    flex-basis: 25%;
    min-height: 315px;
    -webkit-animation: showUp 1s ease;
            animation: showUp 1s ease;
}

span.text {
    min-height: 55px;
}


@-webkit-keyframes showUp {
    0% {
        opacity: 0%;
    }

    100% {
        opacity: 100%;
    }
}


@keyframes showUp {
    0% {
        opacity: 0%;
    }

    100% {
        opacity: 100%;
    }
}
form {
    padding: 50px 0;
}


.card-input {
    width: 50%;
    padding: 5px 10px;
    font-weight: bold;
    outline: none;
}
.popup {
    position: relative;
    width: 30%;
    height: 30%;
    background: rgba(255, 255, 255, 0.15);
    box-shadow: 0 8px 32px 0 rgba(56, 56, 56, 0.37);
    backdrop-filter: blur(6px);
    -webkit-backdrop-filter: blur(6px);
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.18);
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

h3 {
    position: absolute;
    top: 20%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.popup__input {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.popup__input-change {
    padding: 10px 15px;
    margin-bottom: 10px;
}
